@font-face {
  font-family: "Bangers";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("./fonts/bangers-v13-latin-regular.woff2") format("woff2"), url("./fonts/bangers-v13-latin-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("./fonts/roboto-v29-latin-regular.woff2") format("woff2"), url("./fonts/roboto-v29-latin-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("./fonts/roboto-v29-latin-700.woff2") format("woff2"), url("./fonts/roboto-v29-latin-700.woff") format("woff");
}
html {
  font-size: clamp(14px, 11px + 0.35vw, 50px);
}

html,
body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  background: #402DB3;
  font-family: Roboto;
  color: white;
  display: flex;
  flex-direction: column;
}

.pages {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}

.page {
  display: none;
}
.page.active {
  display: flex;
}

.hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.4s ease;
}
.hidden.show {
  opacity: 1;
  pointer-events: initial;
}

h1 {
  font-family: Bangers;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.04em;
  margin-bottom: 2em;
  text-align: center;
}

h2 {
  font-family: Bangers;
  font-size: 1.8rem;
  letter-spacing: 0.05em;
  font-weight: 400;
}

.page.home {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.page.country-nerd {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}
.page.country-nerd .letters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.6rem;
  max-width: 45rem;
}
.page.country-nerd .letters.hide .letter {
  opacity: 0;
}
.page.country-nerd .letters .letter:nth-child(1) {
  transition: opacity 0.2s ease 0.1s;
}
.page.country-nerd .letters .letter:nth-child(2) {
  transition: opacity 0.2s ease 0.2s;
}
.page.country-nerd .letters .letter:nth-child(3) {
  transition: opacity 0.2s ease 0.11s;
}
.page.country-nerd .letters .letter:nth-child(4) {
  transition: opacity 0.2s ease 0.3s;
}
.page.country-nerd .letters .letter:nth-child(5) {
  transition: opacity 0.2s ease 0.18s;
}
.page.country-nerd .letters .letter:nth-child(6) {
  transition: opacity 0.2s ease 0.13s;
}
.page.country-nerd .letters .letter:nth-child(7) {
  transition: opacity 0.2s ease 0.21s;
}
.page.country-nerd .letters .letter:nth-child(8) {
  transition: opacity 0.2s ease 0.05s;
}
.page.country-nerd .letters .letter:nth-child(9) {
  transition: opacity 0.2s ease 0.35s;
}
.page.country-nerd .letters .letter:nth-child(10) {
  transition: opacity 0.2s ease 0.4s;
}
.page.country-nerd .letters .letter:nth-child(11) {
  transition: opacity 0.2s ease 0.22s;
}
.page.country-nerd .letters .letter:nth-child(12) {
  transition: opacity 0.2s ease 0.17s;
}
.page.country-nerd .letters .letter:nth-child(13) {
  transition: opacity 0.2s ease 0.03s;
}
.page.country-nerd .letters .letter:nth-child(14) {
  transition: opacity 0.2s ease 0.09s;
}
.page.country-nerd .letters .letter:nth-child(15) {
  transition: opacity 0.2s ease 0.11s;
}
.page.country-nerd .letters .letter:nth-child(16) {
  transition: opacity 0.2s ease 0.4s;
}
.page.country-nerd .letters .letter:nth-child(17) {
  transition: opacity 0.2s ease 0.35s;
}
.page.country-nerd .letters .letter:nth-child(18) {
  transition: opacity 0.2s ease 0.31s;
}
.page.country-nerd .letters .letter:nth-child(19) {
  transition: opacity 0.2s ease 0.2s;
}
.page.country-nerd .letters .letter:nth-child(20) {
  transition: opacity 0.2s ease 0.14s;
}
.page.country-nerd .letters .letter:nth-child(21) {
  transition: opacity 0.2s ease 0.25s;
}
.page.country-nerd .letters .letter:nth-child(22) {
  transition: opacity 0.2s ease 0.06s;
}
.page.country-nerd .letters .letter:nth-child(23) {
  transition: opacity 0.2s ease 0.17s;
}
.page.country-nerd .letters .letter:nth-child(24) {
  transition: opacity 0.2s ease 0.24s;
}
.page.country-nerd .letters .letter:nth-child(25) {
  transition: opacity 0.2s ease 0.36s;
}
.page.country-nerd .letters .letter:nth-child(26) {
  transition: opacity 0.2s ease 0.37s;
}
.page.country-nerd .letter {
  height: 4rem;
  width: 4rem;
  border-radius: 0.4rem;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: black;
  font-size: 2rem;
  font-family: Bangers;
  box-shadow: 2px 2px 5px 4px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  will-change: transform;
}
.page.country-nerd .letter:hover {
  transform: translate(0.05rem, 0.05rem);
  box-shadow: 0 0 5px 4px rgba(0, 0, 0, 0.15);
}
.page.country-nerd .letter.completed {
  background: radial-gradient(43.04% 43.04% at 67.68% 35.44%, #F3CC3F 0%, #FFE600 0.01%, #FFCB46 85.94%);
}
.page.country-nerd .title-container {
  position: relative;
}
.page.country-nerd .title-container .game-title {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  font-family: Bangers;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.04em;
  white-space: nowrap;
  line-height: 1.3em;
}
.page.country-nerd .title-container .letter-placeholder {
  height: 2.5rem;
  width: 2.5rem;
  display: inline-flex;
  transform: translate(0.1em, -0.2em);
}
.page.country-nerd .answer-container {
  display: none;
  opacity: 0;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
.page.country-nerd .input-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.page.country-nerd .answer-input {
  padding: 0 2rem;
  border: none;
  outline: none;
  border-radius: 1000px;
  font-size: 2rem;
  text-transform: uppercase;
  height: 4rem;
  max-width: 40%;
}
@media (min-width: 768px) {
  .page.country-nerd .answer-input {
    max-width: 30rem;
  }
}
.page.country-nerd .validate-input {
  height: 4rem;
  width: 6rem;
  border-radius: 1000px;
  background-color: white;
  border: none;
  outline: none;
  font-size: 1.8rem;
  font-weight: 700;
  cursor: pointer;
}
.page.country-nerd .answers {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
  width: 100%;
}
.page.country-nerd .answers .answer {
  position: relative;
  padding: 0.5rem;
  border-bottom: 1px solid white;
  width: 100%;
  min-width: 15rem;
  max-width: 80%;
  font-size: 1.5rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page.country-nerd .answers .answer {
    max-width: 25rem;
  }
}
.page.country-nerd .answers .answer.valid::after {
  content: "";
  position: absolute;
  height: 1.2em;
  width: 1.2em;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-image: url("/img/checkmark.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
.page.country-nerd .tip {
  background-color: #F05050;
  color: white;
  width: fit-content;
  border-radius: 1000px;
  padding: 0.8rem 1.2rem;
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
}

.btn {
  position: relative;
  padding: 1.5rem 3rem;
  width: fit-content;
  min-width: 6rem;
  color: white;
  border-radius: 1000px;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  box-shadow: 0.5rem 0.5rem 3rem 1rem rgba(0, 0, 0, 0.19);
  overflow: hidden;
  transition: transform 0.2s ease;
  will-change: transform;
}
.btn::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #F05050;
  border-radius: 1000px;
  z-index: -1;
}
.btn::after {
  content: "";
  position: absolute;
  height: 30rem;
  width: 30rem;
  top: 50%;
  left: 1%;
  transform: translate(-50%, -50%) scale(0);
  background-color: #ee3939;
  border-radius: 50%;
  z-index: -1;
  transition: transform 0.2s ease;
  will-change: transform;
}
.btn:hover {
  transform: scale(1.05);
}
.btn:hover::after {
  transform: translate(-50%, -50%) scale(1);
}
.btn:active {
  transform: scale(0.95);
}

footer {
  padding: 2rem;
  display: flex;
  justify-content: center;
}
footer .copyrights {
  font-size: 0.7rem;
  opacity: 0.8;
}

.game-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  padding: 2rem;
  width: 70%;
  max-width: 100%;
}
@media (min-width: 768px) {
  .game-card {
    width: 15rem;
  }
}
.game-card::before {
  content: "";
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #231961;
  border-radius: 2rem;
  transition: transform 0.2s ease;
  will-change: transform;
}
.game-card .card-title {
  margin-top: 0;
  position: relative;
}
.game-card .card-title .icon {
  position: absolute;
  top: -0.2em;
  right: -0.4em;
  display: block;
  height: 1.5em;
  width: 1.5em;
  background-size: 70%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: white;
  border-radius: 50%;
  margin-left: 0.5rem;
}
.game-card .card-title .icon.icon-country {
  background-image: url("/img/icon-country-nerd.svg");
}
.game-card .card-title .icon.icon-capital {
  background-image: url("/img/icon-capital-city-nerd.svg");
}
.game-card .card-title .icon.icon-flag {
  background-image: url("/img/icon-flag-nerd.svg");
}
.game-card .card-link {
  color: white;
  text-decoration: none;
  max-width: 10rem;
}
.game-card .card-link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.game-card .card-bottom {
  display: flex;
  justify-content: center;
}
.game-card .card-bottom .btn {
  transition: transform 0.2s ease;
  will-change: transform;
}
.game-card:hover::before {
  transform: scale(1.05);
}
.game-card:hover .btn {
  transform: scale(1.15);
}

header {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
}
header .logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  height: 4rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: white;
  text-decoration: none;
  background-color: #231961;
  border-radius: 1000px;
  padding: 0 1.4rem;
}
header .logo img {
  width: 1.7rem;
}
header .menu {
  height: 4rem;
  width: 4rem;
  background-color: #231961;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  box-shadow: 0.1rem 0.1rem 1rem 0.5rem rgba(0, 0, 0, 0.08);
}
header .menu::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 50%;
  top: 38%;
  right: 26%;
  background-color: white;
}
header .menu::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 40%;
  top: 55%;
  right: 26%;
  background-color: white;
}